.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 20px;
    align-items: center;
}

.login__logo{
    object-fit:contain;
    width:100px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.login__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;

}
.login__container> h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container> form> h5{
    margin-bottom: 5px;
}
.login__container>form>input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}
.login__container> form> p{
    margin-top: 15px;
    font-size: 12px;
}
.login__signIn{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;

}
.login__registerButton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    border: 1px solid;
    color: #111;
}